*,
*::before,
*::after {
	box-sizing: border-box;
	padding: 0;
	margin: 0;
}

html {
	overflow-y: scroll;
	scroll-behavior: smooth;
}

html,
body {
	width: 100%;
	overflow-x: hidden;
	overflow-y: scroll;
	scroll-behavior: smooth;
	min-height: calc(var(--vh, 1vh) * 100);
	font-family: 'GT Walsheim', 'Helvetica Neue', sans-serif;
	line-height: 1.6;
}

body {
	display: flex;
	flex-direction: column;
}

img {
	height: auto;
	width: 100%;
	vertical-align: bottom;
}

ul {
	margin: 0;
	padding: 0;
}

li {
	list-style: none;
}

a {
	text-decoration: none;
	color: inherit;
	cursor: pointer;
}

button {
	background-color: transparent;
	color: inherit;
	border-width: 0;
	padding: 0;
	cursor: pointer;
}

input[type='text'],
input[type='password'],
input[type='email'],
input[type='search'],
input[type='submit'],
input[type='tel'],
input[type='url'],
[type='textarea'],
textarea,
select {
	border-radius: 0;
	appearance: none;
	outline: none;
	font-family: inherit;
}

a:focus-visible,
button:focus-visible,
input:focus-visible {
	outline: 4px solid #1b8ffc;
	outline-offset: 2px;
}
